import { useContext, useEffect, useState } from "react";
import { SidebarContext } from "context/SidebarContext";

const useToggleDrawer = () => {
  const [serviceId, setServiceId] = useState("");
  const [bookingUser, setBookingUser] = useState("");
  const [bookingId, setBookingId] = useState("");
  const [allId, setAllId] = useState([]);
  const [title, setTitle] = useState("");
  const { toggleDrawer, isDrawerOpen, toggleModal, toggleBulkDrawer } =
    useContext(SidebarContext);

  console.log(serviceId, "serviceId1");

  const handleAdd = (user, booking_id) => {
    setBookingUser(user);
    setBookingId(booking_id);
    toggleDrawer();
  };

  const handleUpdate = (id, booking_id) => {
    setServiceId(id);
    setBookingId(booking_id);
    toggleDrawer();
  };

  const handleUpdateMany = (id) => {
    setAllId(id);
    toggleBulkDrawer();
  };

  const handleModalOpen = (id, title) => {
    console.log("DELETE", "ww");
    setServiceId(id);
    toggleModal();
    setTitle(title);
  };

  useEffect(() => {
    if (!isDrawerOpen) {
      setServiceId();
      setBookingUser();
      setBookingId();
    }
  }, [isDrawerOpen]);

  const handleDeleteMany = async (id, products) => {
    console.log("ww", "ww");
    setAllId(id);
    toggleModal();
    setTitle("Selected Products");
  };

  return {
    title,
    allId,
    bookingUser,
    setBookingUser,
    handleAdd,
    serviceId,
    bookingId,
    handleUpdate,
    setServiceId,
    handleModalOpen,
    handleDeleteMany,
    handleUpdateMany,
  };
};

export default useToggleDrawer;
